import React, { FC } from 'react'
import clsx from 'clsx'
import format from 'date-fns/format'
import ru from 'date-fns/locale/ru'
import { Typography } from '../../../other/Typography'
import { CalendarHeaderRangeProps } from '../types'
import ArrowNewLeft from '../../../icons/ArrowLeftNew/ArrowLeftNew'
import ArrowNewRight from '../../../icons/ArrowRightNew/ArrowRightNew'
import { Field } from '../../Field'
import '../Calendar.scss'

const CalendarHeaderRange: FC<CalendarHeaderRangeProps> = ({
  date,
  startDate,
  endDate,
  decreaseMonth,
  increaseMonth,
  locale = ru,
  prevMonthButtonDisabled,
  nextMonthButtonDisabled,
  headerFromFieldText,
  headerToFieldText,
}) => {
  return (
    <div className='CalendarNext__HeaderRange'>
      <div className='CalendarNext__HeaderButtons'>
        <button
          className={clsx(
            'reset-button-styles',
            'CalendarNext__HeaderButton',
            '--prev',
          )}
          disabled={prevMonthButtonDisabled}
          onClick={decreaseMonth}
        >
          <ArrowNewLeft />
        </button>

        <Typography
          className='CalendarNext__HeaderDate'
          text={format(date, 'LLLL yyyy', { locale })}
          type='boldBody'
        />
        <button
          className={clsx(
            'reset-button-styles',
            'CalendarNext__HeaderButton',
            '--next',
          )}
          disabled={nextMonthButtonDisabled}
          onClick={increaseMonth}
        >
          <ArrowNewRight />
        </button>
      </div>

      <label className='CalendarNext__HeaderDisplay'>
        <Field
          className='CalendarNext__HeaderFrom'
          fieldSize='XL'
          id='from'
          label={headerFromFieldText}
          onChange={(): void => {}}
          value={startDate}
        />

        <Field
          className='CalendarNext__HeaderTo'
          fieldSize='XL'
          id='to'
          label={headerToFieldText}
          onChange={(): void => {}}
          value={endDate}
        />
      </label>
    </div>
  )
}

CalendarHeaderRange.defaultProps = {
  headerFromFieldText: 'С',
  headerToFieldText: 'По',
}

export default CalendarHeaderRange
